import createFrameArray from './createFrameArray';
import arrayConversion from '../../utils/arrayConversion';
import drawPixelsFromArray from '../../utils/drawPixelsFromArray';

// для изменения размеров фреймов при изменении размеров основного канваса

const frameContainer = document.querySelector('.frame-container').children;

export default function framesResize(mouseAction) {
  for (let i = 0; i < mouseAction.framesArray.length; i += 1) {
    const old = mouseAction.framesArray[i];
    let scale;
    if (mouseAction.unitSize === 4) {
      scale = 1;
    } else if (mouseAction.unitSize === 8) {
      scale = 2;
    } else if (mouseAction.unitSize === 16) {
      scale = 4;
    }
    const newFr = createFrameArray(frameContainer[i].children[4], scale);
    // eslint-disable-next-line no-param-reassign
    mouseAction.framesArray[i] = arrayConversion(old, newFr);
    const canvas = frameContainer[i].children[4];
    const context = canvas.getContext('2d');
    context.clearRect(0, 0, canvas.width, canvas.height);
    drawPixelsFromArray(mouseAction.framesArray[i], frameContainer[i].children[4]);
    localStorage.frames = JSON.stringify(mouseAction.framesArray);
    localStorage.canvas = JSON.stringify(mouseAction.canvasArray.canvasUnits);
    localStorage.unitSize = mouseAction.unitSize;
  }
}
