import getPreview from '../../components/preview/getPreview';
import mouseAction from '../../utils/globalObject';
import drawGrid from './drawGrid';
import drawPixelsFromArray from '../../utils/drawPixelsFromArray';
import arrayConversion from '../../utils/arrayConversion';
import framesResize from '../../components/frame-list/framesResize';

const drawState = mouseAction.canvasArray;

// обработчик для изменения размера канваса
export default function canvasResize() {
  const oldPixelArray = drawState.canvasUnits;
  // берем размер одного "пикселя" из объекта и пересчитываем в зависисмости от нажатой кнопки
  mouseAction.unitSize = document.querySelector('.draw-area').width / Number(this.innerHTML);
  drawGrid(document.querySelector('.draw-area'), mouseAction.unitSize);
  const newPixeArray = arrayConversion(oldPixelArray, drawState.canvasUnits);

  drawPixelsFromArray(newPixeArray, document.querySelector('.draw-area'));
  getPreview(document.querySelector('.animation-block'));
  framesResize(mouseAction);

  localStorage.canvas = JSON.stringify(mouseAction.canvasArray.canvasUnits);
  localStorage.unitSize = mouseAction.unitSize;
}
