import mouseAction from '../../utils/globalObject';

const tools = document.querySelector('.tools');
const toolHotkeys = mouseAction.hotkeys.toolKeys;

export default function toolKeyPressed(e) {
  const modalWindow = document.querySelector('.modal-window');
  if (!modalWindow) {
    for (let i = 0; i < toolHotkeys.length; i += 1) {
      const keyCode = toolHotkeys[i][1];
      const toolClassName = toolHotkeys[i][0];
      if (e.code === keyCode) {
        Array.from(tools.children).forEach((child) => {
          const item = child;
          item.style.border = '';
          item.style.backgroundColor = '';
        });
        if (mouseAction.toolButton === toolClassName) {
          mouseAction.toolButton = '';
        } else {
          document.querySelector(`.${toolClassName}`).style.border = '2px solid green';
          document.querySelector(`.${toolClassName}`).style.backgroundColor = 'yellow';
          mouseAction.toolButton = toolClassName;
        }
      }
    }
  }
}
