// для закрашивания пикселя. Принимает на вход массив "пикселей", номер "пикселя" в массиве,
// цвет для заливки, канвас для отрисовки
export default function drawPixel(pixels, pixelNumber, colorPicker, canvas) {
  const pixelArray = pixels;
  const x = pixelArray[pixelNumber][0];
  const y = pixelArray[pixelNumber][1];
  const xScale = pixelArray[pixelNumber][2] - x;
  const yScale = pixelArray[pixelNumber][3] - y;
  pixelArray[pixelNumber][4] = colorPicker;
  const context = canvas.getContext('2d');
  context.fillStyle = colorPicker;
  context.fillRect(x, y, xScale, yScale);
}
