import mouseAction from '../../utils/globalObject';
import eventTargetExistInPath from '../../utils/eventTargetExistInPath';

let hotKeys;
if (localStorage.getItem('hotkeys') !== null) {
  hotKeys = JSON.parse(localStorage.hotkeys);
  mouseAction.hotkeys = hotKeys;
} else {
  hotKeys = mouseAction.hotkeys;
}

const modal = document.createElement('div');
const main = document.querySelector('main');

const modalHeader = document.createElement('div');
modalHeader.className = 'modal-header';

const modalHeaderDescript = document.createElement('div');
modalHeaderDescript.className = 'modal-header-about';
modalHeaderDescript.innerHTML = '<p>Customize shortcuts</p>';
modalHeader.appendChild(modalHeaderDescript);

const closeModal = document.createElement('div');
closeModal.className = 'close-modal';
closeModal.innerHTML = '<i class="fas fa-times"></i>';
modalHeader.appendChild(closeModal);

const modalFooter = document.createElement('div');
modalFooter.className = 'modal-footer';
const restoreDefault = document.createElement('button');
restoreDefault.className = 'restore-default';
restoreDefault.innerHTML = 'Restore to default';
modalFooter.appendChild(restoreDefault);

const toolKeysChangeDiv = document.createElement('div');
toolKeysChangeDiv.className = 'tool-keys-change';
const toolHead = document.createElement('div');
toolHead.className = 'tool-keys-head';
toolHead.innerHTML = '<p>Tool shortcuts</p>';
toolKeysChangeDiv.appendChild(toolHead);

const frameKeysChangeDiv = document.createElement('div');
frameKeysChangeDiv.className = 'frame-keys-change';
const frameHead = document.createElement('div');
frameHead.className = 'frame-keys-head';
frameHead.innerHTML = '<p>Frame shortcuts</p>';
frameKeysChangeDiv.appendChild(frameHead);

// создаем див для отображеия горячих клавиш, их изменения и описания
function createShortcutDisc() {
  const shortcutDescription = document.createElement('div');
  shortcutDescription.className = 'shortcut-description';
  const shortcutPic = document.createElement('div');
  shortcutPic.className = 'shortcut-picture';
  const shortcutButton = document.createElement('div');
  shortcutButton.className = 'shortcut-button';
  const inputField = document.createElement('span');
  inputField.className = 'shortcut-button-span';
  shortcutButton.appendChild(inputField);
  const shortcutAbout = document.createElement('div');
  shortcutAbout.className = 'shortcut-about';

  shortcutDescription.appendChild(shortcutPic);
  shortcutDescription.appendChild(shortcutButton);
  shortcutDescription.appendChild(shortcutAbout);

  return shortcutDescription;
}

// добавляем элементы циклом в зависомости от количества сохраненных горячих клавиш
for (let i = 0; i < hotKeys.toolKeys.length; i += 1) {
  const elem = createShortcutDisc();
  const toolPic = document.querySelector(`.${hotKeys.toolKeys[i][0]}`).children[0].cloneNode(true);
  const toolKey = hotKeys.toolKeys[i][1];
  const aboutShortcut = hotKeys.toolKeys[i][3];
  elem.querySelector('.shortcut-picture').appendChild(toolPic);
  elem.querySelector('.shortcut-button-span').classList.add(i);
  elem.querySelector('.shortcut-button-span').innerHTML = toolKey.replace(/Key/i, '');
  elem.querySelector('.shortcut-about').innerHTML = aboutShortcut;
  toolKeysChangeDiv.appendChild(elem);
}
for (let i = 0; i < hotKeys.frameKeys.length; i += 1) {
  const elem = createShortcutDisc();
  const frameKey = hotKeys.frameKeys[i][1];
  const aboutShortcut = hotKeys.frameKeys[i][3];
  elem.querySelector('.shortcut-button-span').classList.add(i);
  elem.querySelector('.shortcut-button-span').innerHTML = frameKey.replace(/Key/i, '');
  elem.querySelector('.shortcut-about').innerHTML = aboutShortcut;
  frameKeysChangeDiv.appendChild(elem);
}

modal.appendChild(modalHeader);
modal.appendChild(toolKeysChangeDiv);
modal.appendChild(frameKeysChangeDiv);
modal.appendChild(modalFooter);

// функция для проверки любого массива на наличие указанной горячей клавиши.
// если клавиша найдена, то ей устанавливается класс denied на 100 мсек.
// на вход принимает код клавиши, проверяемый массив и имя класса дива кнопок
function keyDenied(newShortcut, shortcutArray, shortcutBoxClassName) {
  for (let i = 0; i < shortcutArray.length; i += 1) {
    if (shortcutArray[i][1] === newShortcut) {
      const shortcutBox = document.querySelector(`.${shortcutBoxClassName}`);
      const deniedKey = shortcutBox.querySelectorAll('.shortcut-button-span')[i];
      deniedKey.classList.add('denied');
      setTimeout(() => {
        deniedKey.classList.remove('denied');
      }, 100);
      return true;
    }
  } return false;
}

// функция проверяет все массивы горячих клавиш.
function checkShortcut(newShortcut) {
  const toolArray = mouseAction.hotkeys.toolKeys;
  const frameArray = mouseAction.hotkeys.frameKeys;
  if (keyDenied(newShortcut, toolArray, 'tool-keys-change')
    || keyDenied(newShortcut, frameArray, 'frame-keys-change')) {
    return true;
  } return false;
}

// функция для навешивания на все спаны (где отображаются забинденные горячие клавиши)
// onclick'ов, каждый из которых при срабатывании добавляет к своему элементу
// имя класса changed, вешает на документ onkeyup, который срабатывает только один раз,
// запоминая нажатую клавишу и занося ее в качестве новой горячей клавиши в массив,
// если такая клавиша еще не использовалась.
function changeShortcuts() {
  const spans = document.querySelectorAll('.shortcut-button-span');
  Array.from(spans).forEach((elem) => {
    const item = elem;
    item.onclick = (ev) => {
      const currentElement = ev.target;
      if (!item.classList.contains('changed') && !document.querySelector('.changed')) {
        item.classList.add('changed');
      } else if (document.querySelector('.changed')) {
        document.querySelector('.changed').classList.remove('changed');
      }
      if (document.querySelector('.changed')) {
        document.onkeyup = (event) => {
          const newShortcut = event.code;
          if (newShortcut) {
            item.classList.remove('changed');
            const buttonShortcutPosition = currentElement.classList[1];
            if (!checkShortcut(newShortcut)) {
              if (currentElement.parentNode.parentNode.parentNode.className === 'tool-keys-change') {
                mouseAction.hotkeys.toolKeys[buttonShortcutPosition][1] = newShortcut;
                currentElement.innerHTML = newShortcut.replace(/Key/i, '');
              } else if (currentElement.parentNode.parentNode.parentNode.className === 'frame-keys-change') {
                mouseAction.hotkeys.frameKeys[buttonShortcutPosition][1] = newShortcut;
                currentElement.innerHTML = newShortcut.replace(/Key/i, '');
              }
              localStorage.hotkeys = JSON.stringify(mouseAction.hotkeys);
            }
            document.onkeyup = false;
          }
        };
      } else {
        document.onkeyup = false;
      }
    };
  });
}

// восстановление клавиш по умолчанию
function restoreToDefault() {
  const defaultToolKeys = mouseAction.hotkeys.toolKeys;
  const defaultFrameKeys = mouseAction.hotkeys.frameKeys;
  for (let i = 0; i < defaultToolKeys.length; i += 1) {
    const toolKey = defaultToolKeys[i][2];
    mouseAction.hotkeys.toolKeys[i][1] = toolKey;
    const toolSide = document.querySelector('.tool-keys-change');
    toolSide.querySelectorAll('.shortcut-button-span')[i].innerHTML = toolKey.replace(/Key/i, '');
  }
  for (let i = 0; i < defaultFrameKeys.length; i += 1) {
    const frameKey = defaultFrameKeys[i][2];
    mouseAction.hotkeys.frameKeys[i][1] = frameKey;
    const frameSide = document.querySelector('.frame-keys-change');
    frameSide.querySelectorAll('.shortcut-button-span')[i].innerHTML = frameKey.replace(/Key/i, '');
  }
}

export default function modalWindow(e) {
  if (eventTargetExistInPath(e, 'shortcuts-modal')) {
    modal.className = 'modal-window';
    if (!document.querySelector('.modal-window')) {
      main.appendChild(modal);
      changeShortcuts();
    } else if (document.querySelector('.modal-window')) {
      main.removeChild(document.querySelector('.modal-window'));
    }
  } else if (document.querySelector('.modal-window') && !eventTargetExistInPath(e, 'modal-window')) {
    main.removeChild(document.querySelector('.modal-window'));
  } else if (eventTargetExistInPath(e, 'close-modal')) {
    main.removeChild(document.querySelector('.modal-window'));
  } else if (eventTargetExistInPath(e, 'restore-default')) {
    restoreToDefault();
  }
}
