import drawPixelsFromArray from './drawPixelsFromArray';
import mouseAction from './globalObject';

// отрисовка фрейма в основном канвасе
export default function drawActiveFrame() {
  const selected = document.getElementById('selected');
  const frameNumber = +selected.children[0].innerHTML - 1;
  const source = mouseAction.framesArray[frameNumber];
  const target = mouseAction.canvasArray.canvasUnits;
  for (let i = 0; i < source.length; i += 1) {
    const sourceColor = source[i][4];
    target[i][4] = sourceColor;
  }
  const canvas = document.querySelector('.draw-area');
  const context = canvas.getContext('2d');
  context.clearRect(0, 0, canvas.width, canvas.height);
  drawPixelsFromArray(target, document.querySelector('.draw-area'));
}
