import mouseAction from '../../utils/globalObject';
import drawPixel from '../../utils/drawPixel';

// инвертируем цвет в канвасе

function padZero(str, length) {
  let len = length;
  len = len || 2;
  const zeros = new Array(len).join('0');
  return (zeros + str).slice(-len);
}

function invertColor(colorInHex) {
  let hex = colorInHex;
  if (hex.indexOf('#') === 0) {
    hex = hex.slice(1);
  }
  // convert 3-digit hex to 6-digits.
  if (hex.length === 3) {
    hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2];
  }
  if (hex.length !== 6) {
    throw new Error('Invalid HEX color.');
  }
  // invert color components
  const r = (255 - parseInt(hex.slice(0, 2), 16)).toString(16);
  const g = (255 - parseInt(hex.slice(2, 4), 16)).toString(16);
  const b = (255 - parseInt(hex.slice(4, 6), 16)).toString(16);
  // pad each with zeros and return
  return `#${padZero(r)}${padZero(g)}${padZero(b)}`;
}

export default function oppositeColorTool(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  if (mouseAction.toolButton === 'opposite-color') {
    if (e.type === 'mouseup') {
      for (let i = 0; i < pixelArray.length; i += 1) {
        if (pixelArray[i][4] !== '') {
          drawPixel(pixelArray, i, invertColor(pixelArray[i][4]), document.querySelector('.draw-area'));
        }
      }
    }
  }
}
