import mouseAction from '../../utils/globalObject';

// переменная для хранения "пикселей" канваса
const drawState = {};
// переменная для начального размер одного "пикселя" в настоящих пикселях
let defaultScale;

if (localStorage.getItem('unitSize') !== null) {
  defaultScale = +localStorage.unitSize;
} else {
  defaultScale = 16;
}
// заносим его в общий объект
mouseAction.unitSize = defaultScale;

// рисуем сетку и добавляем каждый "пиксель" в объект drawState
function drawGrid(canvas, scale) {
  const context = canvas.getContext('2d');
  const pixelArray = [];
  const pixelColor = '';
  context.clearRect(0, 0, canvas.width, canvas.height);

  for (let row = 0; row < canvas.width; row += scale) {
    for (let col = 0; col < canvas.height; col += scale) {
      if (row + scale <= canvas.width || col + scale <= canvas.height) {
        // создаем массив по каждому "пикселю" в канвасе, где первые 4 цифры -
        // координаты "пикселя" из настоящих пикселей,
        // потом - цвет этого "пикселя", потом - его координаты в канвасе с учетом scale
        // (т.е. второй пиксель в ряду будет иметь координаты (0,1), третий - (0,2) и т.д.)
        const pixel = [row, col, row + scale, col + scale, pixelColor, row / scale, col / scale];
        pixelArray.push(pixel);
      }
    }
  }
  drawState.canvasUnits = pixelArray;

  // показываем размер канваса
  document.querySelector('.canvas-size').innerHTML = `[${pixelArray[pixelArray.length - 1][5] + 1} X ${pixelArray[pixelArray.length - 1][6] + 1}] `;
}

drawGrid(document.querySelector('.draw-area'), defaultScale);
mouseAction.canvasArray = drawState;

export default drawGrid;
