import GIF from 'gif.js.optimized';
import '../../libs/gif.worker';

export default function saveAsGif() {
  const gif = new GIF({
    workers: 2,
    quality: 10,
  });
  const scale = +document.getElementById('save-gif-scale').value;
  const correctCanvasSize = document.querySelector('.canvas-size').innerHTML;
  const correctCanvasWidth = +correctCanvasSize.match(/\d+/g)[0];
  const correctCanvasHeight = +correctCanvasSize.match(/\d+/g)[1];
  const correctFPS = +document.querySelector('.fps-slider').value;
  const canvas = document.createElement('canvas');

  const frames = Array.from(document.querySelectorAll('.frame-back'));

  canvas.setAttribute('width', `${scale * correctCanvasWidth}`);
  canvas.setAttribute('height', `${scale * correctCanvasHeight}`);
  const context = canvas.getContext('2d');

  for (let i = 0; i < frames.length; i += 1) {
    context.clearRect(0, 0, canvas.width, canvas.height);
    context.drawImage(frames[i], 0, 0, canvas.width, canvas.height);
    gif.addFrame(canvas, { copy: true, delay: 1000 / `${correctFPS}` });
  }

  gif.on('finished', (blob) => {
    const exportImage = document.createElement('img');
    exportImage.src = URL.createObjectURL(blob);
    const linkToDownload = document.createElement('a');
    linkToDownload.setAttribute('download', 'New gif.gif');
    linkToDownload.setAttribute('href', URL.createObjectURL(blob));
    linkToDownload.click();
  });
  gif.render();
}
