// функция для нахождения номера пикселя в массиве по значениям его столбца и ряда
export default function findPixelByColumnAndRow(array, column, row) {
  let pixelNumber;
  for (let i = 0; i < array.length; i += 1) {
    if (array[i][5] === column && array[i][6] === row) {
      pixelNumber = i;
    }
  } return pixelNumber;
}
