import drawPixel from './drawPixel';
import mouseAction from './globalObject';

export default function paintWithPickedColor(e, pixelArray, pixelNumber, canvas) {
  if (e.buttons === 1 || e.which === 1) {
    drawPixel(pixelArray, pixelNumber, mouseAction.primaryColor, canvas);
  } else if (e.buttons === 2 || e.which === 3) {
    drawPixel(pixelArray, pixelNumber, mouseAction.secondaryColor, canvas);
  }
}
