import gif from './img/piskel.gif';
import toHeadGif from './img/gifinhead.gif';

export default function landingPageStart() {
  const header = document.createElement('div');
  header.className = 'header';
  const main = document.createElement('div');
  main.className = 'main';
  const landingPage = document.createElement('div');
  landingPage.className = 'landing-page';
  const startButton = document.createElement('button');
  startButton.className = 'start-button';
  startButton.innerHTML = 'Try it!';
  const aboutMe = document.createElement('div');
  aboutMe.className = 'about-author';
  const aboutAuthorImage = document.createElement('img');
  aboutAuthorImage.className = 'about-author-image';
  const paragAbout = document.createElement('div');
  paragAbout.className = 'about-author-text';
  paragAbout.innerHTML = '<p>My name is Yuri Kazlaik</p><p>34 y.o. electronic engineer</p><p>study at RSSChool</p><p><a href="https://github.com/yurikazliak" target="__blank">GitHub account</a></p>';
  const piskelTitle = document.createElement('div');
  piskelTitle.className = 'piskel-title';
  piskelTitle.innerHTML = '<p>This is</p><h1>Piskel-clone</h1><p>almost as good as original piskel :)</p>';

  const implementationDiv = document.createElement('div');
  implementationDiv.className = 'implement-features-list';
  const impUl = document.createElement('ul');
  impUl.className = 'implement-ul';
  impUl.innerHTML = 'Implemented : <li>Different canvas sizes</li><li>Pen tool</li><li>Mirror pen tool tool</li><li>Eraser tool</li><li>Paint bucket tool</li><li>Dithering tool</li><li>Opposite color tool</li><li>Lighten/darker tool</li><li>Paint all pixels in one color tool</li><li>Stroke tool</li><li>Rectangle tool</li><li>Circle tool</<li><li>Full frames management</li><li>Ability to change animation FPS</li><li>Save animation as .gif or .apng</li><li>Local storage</li>';
  implementationDiv.appendChild(impUl);

  const gifAnimation = document.createElement('img');
  gifAnimation.className = 'gif-animation-view';
  gifAnimation.src = gif;

  const headGif = document.createElement('img');
  headGif.className = 'gif-in-head';
  headGif.src = toHeadGif;

  aboutMe.appendChild(aboutAuthorImage);
  aboutMe.appendChild(paragAbout);
  header.appendChild(aboutMe);
  header.appendChild(piskelTitle);
  header.appendChild(headGif);
  main.appendChild(gifAnimation);
  main.appendChild(startButton);
  main.appendChild(implementationDiv);

  landingPage.appendChild(header);
  landingPage.appendChild(main);

  document.body.appendChild(landingPage);

  startButton.onclick = () => {
    setTimeout(() => {
      document.body.removeChild(document.querySelector('.landing-page'));
    }, 666);
  };
}
