import mouseDown from './mouseDown';
import mouseMove from './mouseMove';
import mouseUp from './mouseUp';
import mouseLeave from './mouseLeave';
import chooseColor from '../components/tools/chooseColor';
import canvasResize from '../screens/canvas/canvasResize';
import chooseTool from '../components/tools/chooseTool';
import modalWindow from '../screens/shortcuts';
import toolKeyPressed from '../components/tools/toolsShortcuts';
import toggleFullScreen from '../screens/fullScreen/fullScreen';
import animation from '../components/preview/animation';
import exportAsFile from '../components/save-button';
import googleAuthorization from '../components/authorization/googleAuth';

export default function addListeners() {
  document.querySelector('.draw-area').oncontextmenu = () => false;
  // обработчик на каждую кнопку в tools
  Array.from(document.querySelector('.tools').children).forEach((child) => { const item = child; item.onclick = chooseTool; });

  // обработчик на все кнопки изменения размера канваса
  Array.from(document.querySelector('.size-buttons').children).forEach((child) => {
    const item = child; item.onclick = canvasResize;
  });

  document.getElementById('full').onclick = toggleFullScreen;
  document.querySelector('.fps-slider').addEventListener('change', () => { animation(); });

  chooseColor();
  document.querySelector('.draw-area').addEventListener('mousedown', (e) => { mouseDown(e); });
  document.querySelector('.draw-area').addEventListener('mousemove', (e) => { mouseMove(e); });
  document.querySelector('.draw-area').addEventListener('mouseup', (e) => { mouseUp(e); });
  document.querySelector('.draw-area').addEventListener('mouseleave', (e) => { mouseLeave(e); });
  document.addEventListener('keyup', (e) => { toolKeyPressed(e); });

  document.addEventListener('click', (e) => {
    modalWindow(e);
    exportAsFile(e);
  });
  googleAuthorization();
}
