export default function getPixelNumber(pixelArray, coordX, coordY) {
  let pixelNumber;
  for (let i = 0; i < pixelArray.length; i += 1) {
    if (coordX >= pixelArray[i][0] && coordX <= pixelArray[i][2]
            && coordY >= pixelArray[i][1] && coordY <= pixelArray[i][3]) {
      pixelNumber = i;
    }
  } return pixelNumber;
}
