// получаем изображение от канваса и вешаем его как бэк на target (превью и фрейм)
export default function getPreview(canvas) {
  const target = canvas;
  const dataUrl = document.querySelector('.draw-area').toDataURL();
  target.style.backgroundImage = `url(${dataUrl})`;
  target.style.backgroundPosition = 'center';
  target.style.backgroundSize = 'contain';
  target.style.backgroundRepeat = 'no-repeat';
}
