import './save-style.css';
import eventTargetExistInPath from '../../utils/eventTargetExistInPath';
import saveAsGif from './saveAsGif';
import saveAsApng from './saveAsApng';

export default function exportAsFile(e) {
  if (eventTargetExistInPath(e, 'save-button-gif')) {
    saveAsGif();
  } else if (eventTargetExistInPath(e, 'save-button-apng')) {
    saveAsApng();
  }
}
