export default function createNewFrame(frameCount) {
  const newFrame = document.createElement('div');
  newFrame.className = 'frame';
  // newFrame.classList.add(`${frameCount}`);
  newFrame.setAttribute('data', `${frameCount}`);
  const frameNumber = document.createElement('button');
  frameNumber.className = 'frame-number-button';
  frameNumber.title = 'Frame number';
  frameNumber.innerText = `${frameCount}`;
  const deleteFrame = document.createElement('button');
  deleteFrame.className = 'delete-frame-button';
  deleteFrame.title = 'Delete frame';
  deleteFrame.innerHTML = '<i class="fas fa-trash"></i>';
  const duplicateFrame = document.createElement('button');
  duplicateFrame.className = 'duplicate-frame-button';
  duplicateFrame.title = 'Duplicate frame';
  duplicateFrame.innerHTML = '<i class="far fa-copy"></i>';
  const moveFrame = document.createElement('button');
  moveFrame.className = 'move-frame-button';
  moveFrame.title = 'Move frame';
  moveFrame.innerHTML = '<i class="fas fa-expand-arrows-alt"></i>';
  const canvasBack = document.createElement('canvas');
  canvasBack.className = 'frame-back';
  canvasBack.width = '128';
  canvasBack.height = '128';

  newFrame.appendChild(frameNumber);
  newFrame.appendChild(deleteFrame);
  newFrame.appendChild(duplicateFrame);
  newFrame.appendChild(moveFrame);
  newFrame.appendChild(canvasBack);

  return newFrame;
}
