/* eslint-disable no-console */
import mouseAction from '../../utils/globalObject';
import paintWithPickedColor from '../../utils/paintWithPickedColor';
import getPixelNumber from '../../utils/getPixelNumber';
import findPixelByColumnAndRow from '../../utils/findPixelByColumnAndRow';

// функция для определения радиуса по длинне самого большого перемещения по одной из осей
function returnRadius(startX, startY, endX, endY) {
  let radius;
  if (Math.abs(endX - startX) > Math.abs(endY - startY)) {
    radius = Math.abs(endX - startX);
  } else if (Math.abs(endY - startY) > Math.abs(endX - startX)) {
    radius = Math.abs(endY - startY);
  } else if (Math.abs(endY - startY) === Math.abs(endX - startX)) {
    radius = Math.abs(endY - startY);
  } return radius;
}

function createCircleArray() {
  // X1, Y1 - координаты центра
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const X1 = pixelArray[getPixelNumber(pixelArray, mouseAction.startX, mouseAction.startY)][5];
  const Y1 = pixelArray[getPixelNumber(pixelArray, mouseAction.startX, mouseAction.startY)][6];
  const X2 = pixelArray[getPixelNumber(pixelArray, mouseAction.endX, mouseAction.endY)][5];
  const Y2 = pixelArray[getPixelNumber(pixelArray, mouseAction.endX, mouseAction.endY)][6];
  const radius = returnRadius(X1, Y1, X2, Y2);
  const existedCoordinatesArray = [];
  const allCoordinatesArray = [];
  let x = 0;
  let y = radius;
  let delta = 1 - 2 * radius;
  let error = 0;
  while (y >= 0) {
    allCoordinatesArray.push([X1 + x, Y1 + y]);
    allCoordinatesArray.push([X1 + x, Y1 - y]);
    allCoordinatesArray.push([X1 - x, Y1 + y]);
    allCoordinatesArray.push([X1 - x, Y1 - y]);
    error = 2 * (delta + y) - 1;
    if (delta < 0 && error <= 0) {
      delta += 2 * (x += 1) + 1;
      continue;
    }
    error = 2 * (delta - x) - 1;
    if (delta > 0 && error > 0) {
      delta -= 2 * (y -= 1) + 1;
      continue;
    }
    x += 1;
    delta += 2 * (x - y);
    y -= 1;
  }
  allCoordinatesArray.forEach((item) => {
    const elem = item;
    if (!existedCoordinatesArray.includes(findPixelByColumnAndRow(pixelArray, elem[0], elem[1]))
    && findPixelByColumnAndRow(pixelArray, elem[0], elem[1]) !== undefined) {
      existedCoordinatesArray.push(findPixelByColumnAndRow(pixelArray, elem[0], elem[1]));
    }
  });
  return existedCoordinatesArray;
}

export default function circleTool(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const canvas = document.querySelector('.draw-area');
  const context = canvas.getContext('2d');
  const beforeMove = mouseAction.canvasArray.canvasAtButtonDown;
  if (mouseAction.toolButton === 'draw-circle') {
    if (e.type === 'mousemove') {
      context.putImageData(beforeMove, 0, 0);
      const oldcolor = [];
      const circleArray = createCircleArray();
      circleArray.forEach((item) => {
        oldcolor.push(pixelArray[item][4]);
        paintWithPickedColor(e, pixelArray, item, canvas);
      });
      for (let i = 0; i < circleArray.length; i += 1) {
        pixelArray[circleArray[i]][4] = oldcolor[i];
      }
    } else if (e.type === 'mouseup') {
      createCircleArray().forEach((item) => {
        paintWithPickedColor(e, pixelArray, item, canvas);
      });
    }
  }
}
