import mouseAction from '../../utils/globalObject';
import drawPixel from '../../utils/drawPixel';
import getPixelNumber from '../../utils/getPixelNumber';
import findPixelByColumnAndRow from '../../utils/findPixelByColumnAndRow';

export default function ditheringTool(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const pixelNumber = getPixelNumber(pixelArray, e.layerX, e.layerY);
  const pixelColor = pixelArray[pixelNumber][4];
  // массив для номеров соседей пикселя
  const pixelCrossArr = [];
  if (mouseAction.toolButton === 'dithering-tool') {
    const col = pixelArray[pixelNumber][5];
    const row = pixelArray[pixelNumber][6];

    const colChangePattern = [-1, 0, 1, 0];
    const rowChangePattern = [0, 1, 0, -1];
    // находим соседа пикселя, проверяем, существуют ли они и заносим в массив
    for (let i = 0; i < 4; i += 1) {
      const newCol = col + colChangePattern[i];
      const newRow = row + rowChangePattern[i];
      const attachedPixel = findPixelByColumnAndRow(pixelArray, newCol, newRow);
      if (attachedPixel !== undefined) {
        pixelCrossArr.push(attachedPixel);
      }
    }
    // если какой-то сосед пикселя выкрашен в основной или второстепенный цвет,
    // то закрашиваем пиксель противоположным цветом.
    if (e.buttons === 1) {
      for (let i = 0; i < pixelCrossArr.length; i += 1) {
        if (pixelArray[pixelCrossArr[i]][4] === mouseAction.primaryColor) {
          drawPixel(pixelArray, pixelNumber, mouseAction.secondaryColor,
            document.querySelector('.draw-area'));
        } else if (pixelArray[pixelCrossArr[i]][4] === mouseAction.secondaryColor || pixelColor === '') {
          drawPixel(pixelArray, pixelNumber, mouseAction.primaryColor,
            document.querySelector('.draw-area'));
        }
      }
    } else if (e.buttons === 2) {
      for (let i = 0; i < pixelCrossArr.length; i += 1) {
        if (pixelArray[pixelCrossArr[i]][4] === mouseAction.secondaryColor) {
          drawPixel(pixelArray, pixelNumber, mouseAction.primaryColor,
            document.querySelector('.draw-area'));
        } else if (pixelArray[pixelCrossArr[i]][4] === mouseAction.primaryColor || pixelColor === '') {
          drawPixel(pixelArray, pixelNumber, mouseAction.secondaryColor,
            document.querySelector('.draw-area'));
        }
      }
    }
  }
}
