import mouseAction from '../../utils/globalObject';

const tools = document.querySelector('.tools');

// обработчик для tools
export default function chooseTool() {
  Array.from(tools.children).forEach((child) => {
    const item = child;
    item.style.border = '';
    item.style.backgroundColor = '';
  });
  if (mouseAction.toolButton === this.className) {
    mouseAction.toolButton = '';
  } else {
    this.style.border = '2px solid green';
    this.style.backgroundColor = 'yellow';
    mouseAction.toolButton = this.className;
  }
}
