import mouseAction from '../../utils/globalObject';
import drawPixel from '../../utils/drawPixel';
import getPixelNumber from '../../utils/getPixelNumber';
import findPixelByColumnAndRow from '../../utils/findPixelByColumnAndRow';

// основная функция для заливки
export default function paintBucketTool(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const pixelNumber = getPixelNumber(pixelArray, e.layerX, e.layerY);
  const visitedPixel = new Set();
  const pixelQueue = [];
  const pixelColor = pixelArray[pixelNumber][4];

  if (mouseAction.toolButton === 'paint-bucket') {
    visitedPixel.add(pixelNumber);
    pixelQueue.push(pixelNumber);
    let loopCount = 0;

    const colChangePattern = [-1, 0, 1, 0];
    const rowChangePattern = [0, 1, 0, -1];
    while (pixelQueue.length > 0) {
      loopCount += 1;
      const currentPixel = pixelQueue.pop();
      const col = pixelArray[currentPixel][5];
      const row = pixelArray[currentPixel][6];

      for (let i = 0; i < 4; i += 1) {
        const newCol = col + colChangePattern[i];
        const newRow = row + rowChangePattern[i];
        const attachedPixel = findPixelByColumnAndRow(pixelArray, newCol, newRow);
        if (attachedPixel !== undefined
          && pixelArray[attachedPixel][4] === pixelColor
          && !visitedPixel.has(attachedPixel)) {
          visitedPixel.add(attachedPixel);
          pixelQueue.push(attachedPixel);
        }
      }

      // Остановка цикла если очень много интераций:
      if (loopCount > 2 * pixelArray.length) {
        break;
      }
    }
    // отрисовываем попиксельно полученный set
    visitedPixel.forEach((elem) => {
      if (e.button === 0) {
        drawPixel(pixelArray, elem,
          mouseAction.primaryColor, document.querySelector('.draw-area'));
      } else if (e.button === 2) {
        drawPixel(pixelArray, elem,
          mouseAction.secondaryColor, document.querySelector('.draw-area'));
      }
    });
  }
}
