// для создания массива на основании размеров канваса и шага
export default function createFrameArray(canvas, scale) {
  const frameCanvas = canvas;
  const frameScale = scale;
  const frameArray = [];
  for (let row = 0; row < frameCanvas.width; row += frameScale) {
    for (let col = 0; col < frameCanvas.height; col += frameScale) {
      if (row + frameScale <= frameCanvas.width || col + frameScale <= frameCanvas.height) {
        const pixel = [row, col, row + frameScale, col + frameScale, '', row / frameScale, col / frameScale];
        frameArray.push(pixel);
      }
    }
  } return frameArray;
}
