const showFPS = document.querySelector('.show-fps');
const sliderFPS = document.querySelector('.fps-slider');

// показываем текущее значение FPS
function showCurrentFPS() {
  showFPS.innerHTML = `${sliderFPS.value} FPS`;
}
showCurrentFPS();

// собираем данные с канвасов фреймов
function addInfoFrames() {
  const frames = [];
  const framesAll = document.querySelectorAll('.frame');
  for (let i = 0; i < framesAll.length; i += 1) {
    const framePixels = framesAll[i].children[4].toDataURL();
    frames.push(framePixels);
  }
  return frames;
}

// функция для рисования фрейма
function draw(frame) {
  const preview = document.querySelector('.animation-block');
  preview.style.backgroundImage = `url(${frame})`;
  preview.style.backgroundPosition = 'center';
  preview.style.backgroundSize = 'contain';
  preview.style.backgroundRepeat = 'no-repeat';
}

let count = 0;
let intervalID;
let frameRate;
// анимация всех фреймов с указанным фреймрейтом
function frameAnimation() {
  intervalID = setInterval(() => {
    const frames = addInfoFrames();
    const frame = frames[count % frames.length];
    draw(frame);
    count += 1;
  }, 1000 / frameRate);
}

export default function animation() {
  showCurrentFPS();
  frameRate = +sliderFPS.value;
  if (frameRate > 0) {
    clearInterval(intervalID);
    frameAnimation();
  } else {
    clearInterval(intervalID);
  }
}

animation();
