/* eslint-disable no-console */
import mouseAction from '../../utils/globalObject';
import getPixelNumber from '../../utils/getPixelNumber';
import paintWithPickedColor from '../../utils/paintWithPickedColor';

// линия
function makeStrokeArray(pixelArray) {
  // берем из mouseAction координаты первой и последней точек нашей прямой,
  // находим им соответсвие в массиве "пикселей",
  // берем из этого массива координаты позиций "пикселей", в зависимости от scale.
  // Дальше считаем "пиксели", входящие в линию, по алгоритму Брезенхэма.
  let x1 = pixelArray[getPixelNumber(pixelArray, mouseAction.startX, mouseAction.startY)][5];
  const x2 = pixelArray[getPixelNumber(pixelArray, mouseAction.endX, mouseAction.endY)][5];
  let y1 = pixelArray[getPixelNumber(pixelArray, mouseAction.startX, mouseAction.startY)][6];
  const y2 = pixelArray[getPixelNumber(pixelArray, mouseAction.endX, mouseAction.endY)][6];
  const deltaX = Math.abs(x2 - x1);
  const deltaY = Math.abs(y2 - y1);
  const signX = x1 < x2 ? 1 : -1;
  const signY = y1 < y2 ? 1 : -1;
  let pixelNumber;
  let error = deltaX - deltaY;
  for (let i = 0; i < pixelArray.length; i += 1) {
    if (pixelArray[i][5] === x2 && pixelArray[i][6] === y2) { pixelNumber = i; }
  }
  // массив, в который будем заносить тольko "пиксели", расположенные на линии
  const pixelsOnLine = [pixelNumber];
  while (x1 !== x2 || y1 !== y2) {
    const error2 = error * 2;
    if (error2 > -deltaY) {
      error -= deltaY;
      x1 += signX;
    }
    if (error2 < deltaX) {
      error += deltaX;
      y1 += signY;
    }
    // опять определяем номер "пикселя" для pixelArray,
    // чтобы только его занести в массив "пикселей"
    for (let i = 0; i < pixelArray.length; i += 1) {
      if (pixelArray[i][5] === x1 && pixelArray[i][6] === y1) { pixelNumber = i; }
    }
    if (!pixelsOnLine.includes(pixelNumber)) {
      pixelsOnLine.push(pixelNumber);
    }
  } return pixelsOnLine;
}

export default function strokeToolDraw(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const canvas = document.querySelector('.draw-area');
  const context = canvas.getContext('2d');
  const beforeMove = mouseAction.canvasArray.canvasAtButtonDown;
  if (mouseAction.toolButton === 'draw-stroke') {
    if (e.type === 'mousedown') {
      const startPixel = getPixelNumber(pixelArray, mouseAction.startX, mouseAction.startY);
      paintWithPickedColor(e, pixelArray, startPixel, canvas);
    } else if (e.type === 'mousemove') {
      context.putImageData(beforeMove, 0, 0);
      const oldcolor = [];
      const strokeArray = makeStrokeArray(pixelArray);
      strokeArray.forEach((item) => {
        oldcolor.push(pixelArray[item][4]);
        paintWithPickedColor(e, pixelArray, item, canvas);
      });
      for (let i = 0; i < strokeArray.length; i += 1) {
        pixelArray[strokeArray[i]][4] = oldcolor[i];
      }
    } else if (e.type === 'mouseup') {
      makeStrokeArray(pixelArray).forEach((item) => {
        paintWithPickedColor(e, pixelArray, item, canvas);
      });
    }
  }
}
