// рисуем целиком канвас на основании массива пикселей
export default function drawPixelsFromArray(pixelsArray, canvasToDraw) {
  const context = canvasToDraw.getContext('2d');
  context.clearRect(0, 0, canvasToDraw.width, canvasToDraw.height);
  for (let i = 0; i < pixelsArray.length; i += 1) {
    const x = pixelsArray[i][0];
    const y = pixelsArray[i][1];
    const xScale = pixelsArray[i][2] - x;
    const yScale = pixelsArray[i][3] - y;
    if (pixelsArray[i][4] !== '') {
      const fillColor = pixelsArray[i][4];
      context.fillStyle = fillColor;
      context.fillRect(x, y, xScale, yScale);
    }
  }
}
