import mouseAction from '../utils/globalObject';
import getPreview from '../components/preview/getPreview';
import penToolDraw from '../components/tools/PenTool';
import eraserToolDraw from '../components/tools/EraserTool';
import strokeToolDraw from '../components/tools/StorkeTool';
import exchangeColor from '../utils/exchangeColor';
import colorPicker from '../components/tools/ColorPickerTool';
import paintAllPixels from '../components/tools/PaintAllPixelsTool';
import paintBucketTool from '../components/tools/PaintBucketTool';
import ditheringTool from '../components/tools/DitheringTool';
import rectagleTool from '../components/tools/RectangleTool';
import circleTool from '../components/tools/CircleTool';
import verticalMirrorPenTool from '../components/tools/VerticalMirrorPenTool';
import oppositeColorTool from '../components/tools/oppositeColorTool';

export default function mouseUp(e) {
  if (mouseAction.isLeftButtonDown === true) {
    mouseAction.isLeftButtonDown = false;
    mouseAction.endX = e.layerX;
    mouseAction.endY = e.layerY;
    penToolDraw(e);
    strokeToolDraw(e);
    eraserToolDraw(e);
    paintAllPixels(e);
    paintBucketTool(e);
    ditheringTool(e);
    rectagleTool(e);
    circleTool(e);
    verticalMirrorPenTool(e);
    oppositeColorTool(e);
    exchangeColor(mouseAction);
    colorPicker(e);
    getPreview(document.querySelector('.animation-block'));
    localStorage.frames = JSON.stringify(mouseAction.framesArray);
    localStorage.canvas = JSON.stringify(mouseAction.canvasArray.canvasUnits);
    localStorage.unitSize = mouseAction.unitSize;
  } else;
}
