import mouseAction from '../../utils/globalObject';
import paintWithPickedColor from '../../utils/paintWithPickedColor';
import getPixelNumber from '../../utils/getPixelNumber';
import findPixelByColumnAndRow from '../../utils/findPixelByColumnAndRow';

function makeRectangleArray(pixelArray, startPixelNumber, endPixelNumber) {
  const startPixelPositionX = pixelArray[startPixelNumber][5];
  const startPixelPositionY = pixelArray[startPixelNumber][6];
  const endPixelPositionX = pixelArray[endPixelNumber][5];
  const endPixelPositionY = pixelArray[endPixelNumber][6];
  const dX = endPixelPositionX - startPixelPositionX;
  const dY = endPixelPositionY - startPixelPositionY;
  const array = [];
  let pixelOnLineX;
  let pixelOnLineY;
  for (let i = 0; i <= Math.abs(dX); i += 1) {
    if (dX === 0) {
      pixelOnLineX = startPixelPositionX;
    } else {
      pixelOnLineX = startPixelPositionX + i * ((Math.abs(dX) / dX));
    }
    const lineX = findPixelByColumnAndRow(pixelArray, pixelOnLineX, startPixelPositionY);
    if (!array.includes(lineX)) {
      array.push(lineX);
    }
    const lineXdX = findPixelByColumnAndRow(pixelArray, pixelOnLineX, endPixelPositionY);
    if (!array.includes(lineXdX)) {
      array.push(lineXdX);
    }
  }
  for (let i = 0; i <= Math.abs(dY); i += 1) {
    if (dY === 0) {
      pixelOnLineY = startPixelPositionY;
    } else {
      pixelOnLineY = startPixelPositionY + i * ((Math.abs(dY) / dY));
    }
    const lineY = findPixelByColumnAndRow(pixelArray, startPixelPositionX, pixelOnLineY);
    if (!array.includes(lineY)) {
      array.push(lineY);
    }
    const lineYdY = findPixelByColumnAndRow(pixelArray, endPixelPositionX, pixelOnLineY);
    if (!array.includes(lineYdY)) {
      array.push(lineYdY);
    }
  }
  return array;
}

export default function rectangleTool(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const canvas = document.querySelector('.draw-area');
  const context = canvas.getContext('2d');
  const startPixel = getPixelNumber(pixelArray, mouseAction.startX, mouseAction.startY);
  const endPixel = getPixelNumber(pixelArray, mouseAction.endX, mouseAction.endY);
  const beforeMove = mouseAction.canvasArray.canvasAtButtonDown;
  if (mouseAction.toolButton === 'draw-rectangle') {
    if (e.type === 'mousedown') {
      paintWithPickedColor(e, pixelArray, startPixel, canvas);
    }
    if (e.type === 'mousemove') {
      context.putImageData(beforeMove, 0, 0);
      const rectangleToDraw = makeRectangleArray(pixelArray, startPixel, endPixel);
      const oldcolor = [];
      rectangleToDraw.forEach((elem) => {
        oldcolor.push(pixelArray[elem][4]);
        paintWithPickedColor(e, pixelArray, elem, canvas);
      });
      for (let i = 0; i < rectangleToDraw.length; i += 1) {
        pixelArray[rectangleToDraw[i]][4] = oldcolor[i];
      }
    }
    if (e.type === 'mouseup') {
      const rectangleToDraw = makeRectangleArray(pixelArray, startPixel, endPixel);
      for (let i = 0; i < rectangleToDraw.length; i += 1) {
        paintWithPickedColor(e, pixelArray, rectangleToDraw[i], canvas);
      }
    }
  }
}
