import mouseAction from '../../utils/globalObject';
import drawPixelsFromArray from '../../utils/drawPixelsFromArray';
import getPreview from '../preview/getPreview';
import createFrameArray from './createFrameArray';
import createNewFrame from './createNewFrame';
import drawActiveFrame from '../../utils/drawActiveFrame';


const frameContainer = document.querySelector('.frame-container');
// счетчик для фреймов
let frameCount = 1;
const addFrameButton = document.querySelector('.add-new-frame');
// создаем пустой массив пикселей фрейма
function frameCanvasArray() {
  let scale;
  if (mouseAction.unitSize === 4) {
    scale = 1;
  } else if (mouseAction.unitSize === 8) {
    scale = 2;
  } else if (mouseAction.unitSize === 16) {
    scale = 4;
  }
  return createFrameArray(document.querySelector('.frame-container').children[0].children[4], scale);
}

mouseAction.framesArray = [frameCanvasArray()];

// обнуляем массив главного канваса и сам канвас
function clearPixelArray() {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  for (let i = 0; i < pixelArray.length; i += 1) {
    pixelArray[i][4] = '';
  }
  const canvas = document.querySelector('.draw-area');
  const context = canvas.getContext('2d');
  context.clearRect(0, 0, canvas.width, canvas.height);
}

// добавляем новый массив пикселей фрейма когда добавляем фрейм
function addToFramesArray() {
  mouseAction.framesArray.push(frameCanvasArray());
}

// установка флага для обозначения активного фрейма
function onAction(elem) {
  for (let i = 0; i < frameContainer.children.length - 1; i += 1) {
    frameContainer.children[i].classList.remove('on-action');
    frameContainer.children[i].removeAttribute('id');
  }
  elem.classList.add('on-action');
  elem.setAttribute('id', 'selected');
  const actionColor = getComputedStyle(document.querySelector('.on-action')).backgroundColor;
  document.querySelector('.drawing-container').style.backgroundColor = `${actionColor}`;
}

// правильная нoмерация фреймов
function correctFrameNumbers() {
  for (let i = 0; i < frameContainer.children.length - 1; i += 1) {
    const numberButton = frameContainer.children[i].children[0];
    numberButton.innerText = `${i + 1}`;
  }
}

// для создания дубликатов
function duplicate(targetFrame) {
  frameCount += 1;
  const oldFrame = targetFrame;
  const duplicated = oldFrame.cloneNode(true);
  // дублируемый массив
  const oldFrameArray = Array.from(mouseAction.framesArray[+oldFrame.children[0].innerHTML - 1]);
  // пустой дубликат
  const duplicateFrame = frameCanvasArray();
  // если у дубликата спрятаны кнопки delete/move, то показываем их
  if (duplicated.children[1].style.display === 'none') {
    duplicated.children[1].style.display = '';
    duplicated.children[3].style.display = '';
  }

  frameContainer.insertBefore(duplicated, oldFrame.nextElementSibling);
  // вешаем на дубликат активный фрейм
  onAction(duplicated);
  // дублируем массив и вставляем его сразу следом
  for (let i = 0; i < oldFrameArray.length; i += 1) {
    const oldColor = oldFrameArray[i][4];
    duplicateFrame[i][4] = oldColor;
  }
  mouseAction.framesArray.splice(+oldFrame.children[0].innerHTML - 1, 0, duplicateFrame);
  drawActiveFrame();
  const duplicateFrameNumber = +duplicated.children[0].innerHTML;
  drawPixelsFromArray(mouseAction.framesArray[duplicateFrameNumber], duplicated.children[4]);
  correctFrameNumbers();

  localStorage.frames = JSON.stringify(mouseAction.framesArray);
  localStorage.canvas = JSON.stringify(mouseAction.canvasArray.canvasUnits);
  localStorage.unitSize = mouseAction.unitSize;
}

// для удаления фрейма
function remove(targetFrame) {
  const oldFrame = targetFrame;
  if (frameCount > 1) {
    // если на фрейме висел on-action, удаляем его
    if (oldFrame.classList.contains('on-action')) {
      const siblingPrev = oldFrame.previousElementSibling;
      const siblingNext = oldFrame.nextElementSibling;
      if (siblingPrev === null) {
        siblingNext.classList.add('on-action');
        siblingNext.setAttribute('id', 'selected');
      } else {
        siblingPrev.classList.add('on-action');
        siblingPrev.setAttribute('id', 'selected');
      }
    }
    frameContainer.removeChild(oldFrame);
    frameCount -= 1;
    drawActiveFrame();

    // удаляем массив пикселей, относящийся к удаляемому фрейму
    mouseAction.framesArray.splice(+oldFrame.children[0].innerHTML - 1, 1);
    correctFrameNumbers();

    localStorage.frames = JSON.stringify(mouseAction.framesArray);
    localStorage.canvas = JSON.stringify(mouseAction.canvasArray.canvasUnits);
    localStorage.unitSize = mouseAction.unitSize;
  }
}

// спрятать/показать кнопки delete/move если фрейм только один
function toggleButtons() {
  if (frameContainer.children.length === 2) {
    document.querySelector('.delete-frame-button').style.display = 'none';
    document.querySelector('.move-frame-button').style.display = 'none';
  } else {
    document.querySelector('.delete-frame-button').style.display = '';
    document.querySelector('.move-frame-button').style.display = '';
  }
}

// установка активного фрейма
function setAction(targetFrame) {
  const oldFrame = targetFrame;
  onAction(oldFrame);
  drawActiveFrame();
  getPreview(document.querySelector('.animation-block'));
}

// ------ drag frame functions ------
let dragObject = {};
dragObject.buttonPressed = false;

// определяем элемент ПОД передвигаемым
function findDroppable(event) {
  // спрячем переносимый элемент
  dragObject.elem.hidden = true;
  // получить самый вложенный элемент под курсором мыши
  const elem = document.elementFromPoint(event.clientX, event.clientY);

  // показать переносимый элемент обратно
  dragObject.elem.hidden = false;
  if (elem == null) {
    // такое возможно, если курсор мыши "вылетел" за границу окна
    return null;
  }
  return elem;
}

function frameDragStart(e) {
  if (frameContainer.children.length > 2) {
    let draggedFrame = null;
    // проверяем таргет
    if (e.target.className === 'frame-back' || e.target.className === 'move-frame-button') {
      draggedFrame = e.target.parentNode;
    } else if (e.target.parentNode.className === 'move-frame-button') {
      draggedFrame = e.target.parentNode.parentNode;
    }
    if (draggedFrame) {
      dragObject.elem = draggedFrame.cloneNode(true);
      dragObject.framePositionInArray = +draggedFrame.children[0].innerHTML - 1;
      dragObject.framePixels = mouseAction.framesArray[dragObject.framePositionInArray];
      dragObject.positionX = draggedFrame.getBoundingClientRect().left;
      dragObject.startY = e.pageY;
      dragObject.buttonPressed = true;
      if (e.target.className === 'move-frame-button'
        || e.target.parentNode.className === 'move-frame-button') {
        // добавляем к месту нажатия половину высоты фрейма
        dragObject.shiftY = e.layerY + 54;
      } else {
        // добавляем к месту нажатия марджин
        dragObject.shiftY = e.layerY - 24;
      }
    }
  }
}

function frameDragMove(e) {
  if (dragObject.buttonPressed === true) {
    // проверяем, на сколько переместилась мышь с зажатой кнопкой
    const moveY = e.pageY - dragObject.startY;
    if (Math.abs(moveY) > 3) {
      // создаем аватар для отображения передвигаемого фрейма
      const avatar = dragObject.elem;
      frameContainer.appendChild(avatar);
      avatar.style.zIndex = 1000;
      avatar.style.position = 'absolute';
      avatar.style.left = `${dragObject.positionX}px`;
      avatar.style.top = `${e.clientY - dragObject.shiftY}px`;
      // отрисовываем пиксели в канвасе фрейма
      drawPixelsFromArray(dragObject.framePixels, avatar.children[4]);
      // элемент под двигаемым фреймом
      const frameUnder = findDroppable(e).closest('.frame');
      // првоерка, есть ли фрейм с классом empty, над которым будет двигаться фрейм
      const emptyInDocument = document.querySelector('.empty');
      if (frameUnder && frameUnder.className !== 'empty' && !emptyInDocument) {
        frameUnder.className = 'empty';
      } else if (frameUnder && emptyInDocument) {
        // если фрейм находится над другим фремом и уже есть класс empty,
        // то передвигаем нынешний фрейм на место предыдущего
        const newEmptyFrameNumber = +frameUnder.children[0].innerHTML - 1;
        const newEmptyPixelsArray = mouseAction.framesArray[newEmptyFrameNumber];
        const oldEmptyFrameNumber = +emptyInDocument.children[0].innerHTML - 1;
        const oldEmptyPixelsArray = mouseAction.framesArray[oldEmptyFrameNumber];
        mouseAction.framesArray[newEmptyFrameNumber] = oldEmptyPixelsArray;
        mouseAction.framesArray[oldEmptyFrameNumber] = newEmptyPixelsArray;
        frameContainer.children[newEmptyFrameNumber].className = 'empty';
        frameContainer.children[oldEmptyFrameNumber].className = 'frame';
        drawPixelsFromArray(newEmptyPixelsArray,
          frameContainer.children[oldEmptyFrameNumber].children[4]);
      }
    }
  }
}

function frameDrop() {
  dragObject.buttonPressed = false;
  if (dragObject.elem) {
    // ищем фрейм с классом empty и заносим передвигаемый фрейм на его место
    const frameUnder = document.querySelector('.empty');
    if (frameUnder) {
      frameUnder.className = 'frame';
      const frameUnderPositionInArray = +frameUnder.children[0].innerHTML - 1;
      mouseAction.framesArray[frameUnderPositionInArray] = dragObject.framePixels;
      drawPixelsFromArray(dragObject.framePixels,
        frameContainer.children[frameUnderPositionInArray].children[4]);
      setAction(frameContainer.children[frameUnderPositionInArray]);
      frameContainer.removeChild(dragObject.elem);

      localStorage.frames = JSON.stringify(mouseAction.framesArray);
      localStorage.canvas = JSON.stringify(mouseAction.canvasArray.canvasUnits);
      localStorage.unitSize = mouseAction.unitSize;
    }
  } else;
  dragObject = {};
}

// ----- end of drag frames functions ------

// 3 функциия для горячих клавиш
function switchFrame(e) {
  let currentframe;
  if (e.target.className === 'frame-number-button'
    || e.target.className === 'frame-back') {
    currentframe = e.target.parentNode;
    setAction(currentframe);
  }
}

function removeFrame(e) {
  let currentframe = null;
  if (e.target.className === 'delete-frame-button') {
    currentframe = e.target.parentNode;
  } else if (e.target.parentNode.className === 'delete-frame-button') {
    currentframe = e.target.parentNode.parentNode;
  }
  if (currentframe) {
    remove(currentframe);
    toggleButtons();
  }
}

function copyFrame(e) {
  let currentframe = null;
  if (e.target.className === 'duplicate-frame-button') {
    currentframe = e.target.parentNode;
  } else if (e.target.parentNode.className === 'duplicate-frame-button') {
    currentframe = e.target.parentNode.parentNode;
  }
  if (currentframe) {
    duplicate(currentframe);
    toggleButtons();
  }
}

// для создания фрейма
function createFrame() {
  frameCount += 1;
  const newFrame = createNewFrame(frameCount);

  frameContainer.insertBefore(newFrame, addFrameButton);

  onAction(newFrame);
  addToFramesArray();
  clearPixelArray();
  getPreview(document.querySelector('.animation-block'));
  toggleButtons();
  localStorage.frames = JSON.stringify(mouseAction.framesArray);
  localStorage.canvas = JSON.stringify(mouseAction.canvasArray.canvasUnits);
  localStorage.unitSize = mouseAction.unitSize;
}

// вешаем горячие клавиши
function pressedHotKeys(e) {
  const actionFrame = document.querySelector('.on-action');
  const frameHotkeys = mouseAction.hotkeys.frameKeys;
  const modalWindow = document.querySelector('.modal-window');
  if (!modalWindow) {
    if (e.code === frameHotkeys[0][1]) {
      createFrame();
    } else if (e.code === frameHotkeys[1][1]) {
      remove(actionFrame);
    } else if (e.code === frameHotkeys[2][1]) {
      duplicate(actionFrame);
    } else if (e.code === frameHotkeys[3][1]) {
      const prevFrame = actionFrame.previousElementSibling;
      if (prevFrame) {
        setAction(prevFrame);
      }
    } else if (e.code === frameHotkeys[4][1]) {
      const nextFrame = actionFrame.nextElementSibling;
      if (nextFrame && nextFrame.className !== 'add-new-frame') {
        setAction(nextFrame);
      }
    }
  }
}

// работа с local storage
if (localStorage.getItem('frames') !== null) {
  const framesArray = JSON.parse(localStorage.frames);
  mouseAction.framesArray = framesArray;
  if (framesArray.length > 1) {
    for (let i = 0; i < framesArray.length - 1; i += 1) {
      const newFrame = createNewFrame(i + 2);
      frameContainer.insertBefore(newFrame, addFrameButton);
    }
  }
  for (let i = 0; i < framesArray.length; i += 1) {
    drawPixelsFromArray(framesArray[i],
      frameContainer.children[i].children[4]);
  }
  frameCount = framesArray.length;
}

export default function frameList() {
  toggleButtons();
  frameContainer.onmousedown = (e) => {
    switchFrame(e);
    removeFrame(e);
    copyFrame(e);
    frameDragStart(e);
  };

  frameContainer.onmousemove = (e) => {
    e.preventDefault();
    frameDragMove(e);
  };

  frameContainer.onmouseup = () => {
    frameDrop();
  };
  addFrameButton.onclick = createFrame;

  document.addEventListener('keyup', (e) => { pressedHotKeys(e); });
}
