import mouseAction from '../../utils/globalObject';
import getPixelNumber from '../../utils/getPixelNumber';
import paintWithPickedColor from '../../utils/paintWithPickedColor';
import findPixelByColumnAndRow from '../../utils/findPixelByColumnAndRow';

function findOppositePixel(pixelArray, pixelNumber) {
  const startX = pixelArray[pixelNumber][5];
  const startY = pixelArray[pixelNumber][6];
  const maximumX = pixelArray[pixelArray.length - 1][5];
  const oppositeX = maximumX - startX;
  const oppositePixel = findPixelByColumnAndRow(pixelArray, oppositeX, startY);
  return oppositePixel;
}

export default function verticalMirrorPenTool(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const pixelNumber = getPixelNumber(pixelArray, e.layerX, e.layerY);
  const canvas = document.querySelector('.draw-area');
  if (mouseAction.toolButton === 'vertical-mirror-pen-tool') {
    paintWithPickedColor(e, pixelArray, pixelNumber, canvas);
    paintWithPickedColor(e, pixelArray, findOppositePixel(pixelArray, pixelNumber), canvas);
  }
}
