/* eslint-disable camelcase */
/* eslint-disable no-console */
import './authorization.css';
import avatar from './img/_avatar180.jpg';

const userImageTarger = document.querySelector('.user-picture');
userImageTarger.src = avatar;
const userName = document.querySelector('.user-name');
userName.innerHTML = 'Welcome, user!';

const state = {
  name: null,
  imgUrl: null,
};

const onInit = () => {
  console.log('init OK');
};
const onError = (err) => {
  console.log('error', err);
};

window.gapi.load('auth2', () => {
  window.gapi.auth2
    .init({
      client_id: '386767258811-g12picgjklcv4pu52sneatvbdln32jg0.apps.googleusercontent.com',
    })
    .then(onInit, onError);
});

const signIn = () => {
  const auth2 = window.gapi.auth2.getAuthInstance();
  auth2.signIn().then((googleUser) => {
    const profile = googleUser.getBasicProfile();
    state.name = profile.getName();
    state.imgUrl = profile.getImageUrl();
    userName.innerHTML = `Welcome, ${profile.getName()}`;
    userImageTarger.src = `${profile.getImageUrl()}`;
  });
};

const signOut = () => {
  const auth2 = window.gapi.auth2.getAuthInstance();
  auth2.signOut().then(() => {
    state.name = null;
    state.imgUrl = null;
  });
  userName.innerHTML = 'Welcome, user!';
  userImageTarger.src = avatar;
};

export default function googleAuthorization() {
  document.querySelector('.sign-in-button').onclick = signIn;
  document.querySelector('.sign-out-button').onclick = signOut;
}
