import mouseAction from '../../utils/globalObject';

const primaryColorPicker = document.getElementById('primary-color');
const secondaryColorPicker = document.getElementById('secondary-color');

export default function chooseColor() {
  primaryColorPicker.addEventListener('change', (e) => {
    mouseAction.primaryColor = e.target.value;
  }, false);
  primaryColorPicker.select();
  secondaryColorPicker.addEventListener('change', (e) => {
    mouseAction.secondaryColor = e.target.value;
  }, false);
  secondaryColorPicker.select();
}
