import mouseAction from '../../utils/globalObject';
import drawPixel from '../../utils/drawPixel';
import getPixelNumber from '../../utils/getPixelNumber';
import addLuminToColor from '../../utils/addLuminToColor';

// функция для осветления\затемнения пикселя. Принимает цвет и на сколько осветлять в процентах.
function colorLuminance(color, luminacity) {
  const red = color.slice(1, 3);
  const blue = color.slice(3, 5);
  const green = color.slice(5);

  return `#${addLuminToColor(red, luminacity)}${addLuminToColor(blue, luminacity)}${addLuminToColor(green, luminacity)}`;
}

export default function lightenTool(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const pixelNumber = getPixelNumber(pixelArray, e.layerX, e.layerY);
  const pixelColor = pixelArray[pixelNumber][4];
  if (mouseAction.toolButton === 'lighten-tool') {
    if (pixelColor !== '') {
      if (e.buttons === 1) {
        const luminColor = colorLuminance(pixelColor, 10);
        drawPixel(pixelArray, pixelNumber, luminColor, document.querySelector('.draw-area'));
      } else if (e.buttons === 2) {
        const luminColor = colorLuminance(pixelColor, -10);
        drawPixel(pixelArray, pixelNumber, luminColor, document.querySelector('.draw-area'));
      }
    }
  }
}
