import drawPixelsFromArray from './drawPixelsFromArray';

// берем массив из большого канвас, заносим его в массив активного фрейма
// и отрисовываем активный фрейм
export default function exchangeColor(mouseAction) {
  const selected = document.getElementById('selected');
  const frameNumber = +selected.children[0].innerText - 1;
  const target = mouseAction.framesArray[frameNumber];
  const source = mouseAction.canvasArray.canvasUnits;
  for (let i = 0; i < source.length; i += 1) {
    const sourceColor = source[i][4];
    target[i][4] = sourceColor;
  }
  drawPixelsFromArray(target, selected.children[4]);
}
