import mouseAction from '../../utils/globalObject';
import drawPixel from '../../utils/drawPixel';
import getPixelNumber from '../../utils/getPixelNumber';

export default function penToolDraw(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const pixelNumber = getPixelNumber(pixelArray, e.layerX, e.layerY);
  if (mouseAction.toolButton === 'pen') {
    if (e.buttons === 1) {
      drawPixel(pixelArray, pixelNumber, mouseAction.primaryColor, document.querySelector('.draw-area'));
    } else if (e.buttons === 2) {
      drawPixel(pixelArray, pixelNumber, mouseAction.secondaryColor, document.querySelector('.draw-area'));
    }
  }
}
