import mouseAction from '../../utils/globalObject';
import drawPixel from '../../utils/drawPixel';
import getPixelNumber from '../../utils/getPixelNumber';

// закрашиваем все одинаковые пиксели одним цветом
export default function paintAllPixels(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const pixelNumber = getPixelNumber(pixelArray, e.layerX, e.layerY);
  if (mouseAction.toolButton === 'paint-all-pixels') {
    const pixelColor = pixelArray[pixelNumber][4];
    for (let i = 0; i < pixelArray.length; i += 1) {
      if (pixelArray[i][4] === pixelColor) {
        if (e.button === 0) {
          drawPixel(pixelArray, i, mouseAction.primaryColor, document.querySelector('.draw-area'));
        } else if (e.button === 2) {
          drawPixel(pixelArray, i, mouseAction.secondaryColor, document.querySelector('.draw-area'));
        }
      }
    }
  }
}
