import mouseAction from '../../utils/globalObject';

const primaryColorPicker = document.getElementById('primary-color');
mouseAction.primaryColor = primaryColorPicker.value;
const secondaryColorPicker = document.getElementById('secondary-color');
mouseAction.secondaryColor = secondaryColorPicker.value;

// 2 функции для превращения значения цвета из rgb в #hex
function componentToHex(component) {
  const hex = component.toString(16);
  return hex.length === 1 ? `0${hex}` : hex;
}
function rgbToHex(r, g, b) {
  return `#${componentToHex(r)}${componentToHex(g)}${componentToHex(b)}`;
}

export default function colorPickerTool(e) {
  const colorSource = e.target;
  if (mouseAction.toolButton === 'color-picker') {
    if (colorSource.className === 'draw-area') {
      const x = e.layerX;
      const y = e.layerY;
      const ctx = colorSource.getContext('2d');
      const pixelData = ctx.getImageData(x, y, 1, 1).data;
      const pickedColor = rgbToHex(pixelData[0], pixelData[1], pixelData[2]);
      if (e.button === 0) {
        primaryColorPicker.value = pickedColor;
        mouseAction.primaryColor = primaryColorPicker.value;
      } else if (e.button === 2) {
        secondaryColorPicker.value = pickedColor;
        mouseAction.secondaryColor = secondaryColorPicker.value;
      }
    }
  }
}
