// берем одну из составляющих цвета (red, blue, green) в 16ричном формате,
// конвертируем в 10ричный, добавляем значение процентов осветления,
// конвертируем полученное значение обратно в 16ричный формат и возвращаем его.
export default function addLuminToColor(colorInHex, luminacity) {
  let colorInDecimal = parseInt(colorInHex, 16);
  colorInDecimal += luminacity;
  if (colorInDecimal > 255) {
    colorInDecimal = 255;
  } else if (colorInDecimal < 0) {
    colorInDecimal = 0;
  }
  let resultedColor = Number(colorInDecimal).toString(16);
  if (resultedColor.length < 2) {
    resultedColor = `0${resultedColor}`;
  }
  return resultedColor;
}
