import mouseAction from '../../utils/globalObject';
import getPixelNumber from '../../utils/getPixelNumber';

// стирка
export default function eraserToolDraw(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  const pixelNumber = getPixelNumber(pixelArray, e.layerX, e.layerY);
  const canvas = document.querySelector('.draw-area');
  const context = canvas.getContext('2d');
  if (mouseAction.toolButton === 'eraser') {
    if (pixelArray[pixelNumber][4] !== '') {
      const x = pixelArray[pixelNumber][0];
      const y = pixelArray[pixelNumber][1];
      const windth = pixelArray[pixelNumber][2] - x;
      const height = pixelArray[pixelNumber][3] - y;
      context.clearRect(x, y, windth, height);
      pixelArray[pixelNumber][4] = '';
    }
  }
}
