import UPNG from 'upng-js';

const img = document.createElement('img');
img.className = 'apng-image';

export default function saveAsApng() {
  const framesArray = Array.from(document.querySelectorAll('.frame-back'));
  const images = [];
  const delaysArr = [];
  const correctFPS = +document.querySelector('.fps-slider').value;
  const delay = 1000 / correctFPS;
  for (let i = 0; i < framesArray.length; i += 1) {
    const context = framesArray[i].getContext('2d');
    const oneItem = context.getImageData(0, 0, framesArray[i].width, framesArray[i].height).data;
    images.push(oneItem);
    delaysArr.push(delay);
  }
  const bufer = UPNG.encode(images, 128, 128, 250, delaysArr);
  const blob = new Blob([bufer], { type: 'image/png' });
  const url = URL.createObjectURL(blob);
  img.src = url;
  const linkToDownload = document.createElement('a');
  linkToDownload.setAttribute('download', 'New animation.png');
  linkToDownload.setAttribute('href', URL.createObjectURL(blob));
  linkToDownload.click();
}
